import React, { useCallback, useState } from 'react';

import { useNavigation } from '@react-navigation/native';

import { AntDesign } from '@expo/vector-icons';

import { useCoffee } from '../../hooks/coffee';

import { 
  Container, 
  Text, 
  BackButtonContainer, 
  BottomContainer,  
  OptionsContainer,
  OptionContainer,
  OptionFrame,
  Image,
  NextButtonContainer,
  NextButtonText,
  OptionText,
} from './styles';

const Informations4: React.FC = () => {
  const [choice, setChoice] = useState<'weak' | 'balanced' | 'intense' | ''>('');

  const { goBack, reset } = useNavigation();

  const { setNewIntensityOfCoffee, savePreferences } = useCoffee();

  const handleNext = useCallback(async () => {
    await savePreferences();
    reset({
      routes: [{ name: 'Home' }],
      index: 0,
    });
  }, [reset, choice]);

  return (
    <Container>
      <BackButtonContainer onPress={() => goBack()}>
        <AntDesign name="arrowleft" size={40} color="#0e0e0e" />
      </BackButtonContainer>
      <Text style={{fontFamily: 'ChelseaMarket_400Regular'}}>
        Qual é a intensidade que você gosta?
      </Text>
      <BottomContainer>
        <OptionsContainer>
          <OptionContainer onPress={() => {
            setChoice('weak');
            setNewIntensityOfCoffee('weak');
          }}>
            <OptionFrame style={[choice === 'weak' && { borderColor: '#1da1f2' }, { backgroundColor: '#8B6047' }]}>
              <Image source={require('../../assets/coffeeCup.png')} resizeMode="contain" />
            </OptionFrame>
            <OptionText style={{fontFamily: 'ChelseaMarket_400Regular'}}>
              Fraco
            </OptionText>
          </OptionContainer>
          <OptionContainer onPress={() => {
            setChoice('balanced');
            setNewIntensityOfCoffee('balanced');
          }}>
            <OptionFrame style={[choice === 'balanced' && { borderColor: '#1da1f2' }, { backgroundColor: '#522F1B' }]}>
              <Image source={require('../../assets/coffeeCup.png')} resizeMode="contain" />
            </OptionFrame>
            <OptionText style={{fontFamily: 'ChelseaMarket_400Regular'}}>
              Equilibrado
            </OptionText>
          </OptionContainer>
          <OptionContainer onPress={() => {
            setChoice('intense');
            setNewIntensityOfCoffee('intense');
          }}>
            <OptionFrame style={[choice === 'intense' && { borderColor: '#1da1f2' }, { backgroundColor: '#270E00' }]}>
              <Image source={require('../../assets/coffeeCup.png')} resizeMode="contain"/>
            </OptionFrame>
            <OptionText style={{fontFamily: 'ChelseaMarket_400Regular'}}>
              Intenso
            </OptionText>
          </OptionContainer>
        </OptionsContainer>
        <NextButtonContainer style={choice === '' && { backgroundColor: '#C8C8C8' }} onPress={handleNext} disabled={choice === ''}>
          <NextButtonText>Pronto</NextButtonText>
        </NextButtonContainer>
      </BottomContainer>
    </Container>
  );
}

export default Informations4;