import React, { useState, createRef, useMemo, useCallback } from 'react';

import { useCoffee } from '../../hooks/coffee';

import { 
  Dimensions, 
  ScrollView, 
} from 'react-native';

import { AntDesign } from '@expo/vector-icons';

import { useNavigation } from '@react-navigation/native';

import { 
  PagesContainer, 
  PageContainer,
  PageImage,
  BlueText,
  PageText,
  BottomNavigationContainer, 
  PageNumberText, 
  HiddenNavigationButtons, 
  HiddenNavigationButton,
  HiddenNavigationButtonExampleText,
  EscapeButton,
  EscapeButtonText,
} from './styles';

const deviceWidth = Dimensions.get('window').width;
const deviceHeight = Dimensions.get('window').height;
const numberOfPages = 11;

const Instructions: React.FC = () => {
  const [page, setPage] = useState(0);
  const [understood, setUnderstood] = useState(false);

  const { gramsOfWater, gramsOfWaterForBloom } = useCoffee();

  const { reset } = useNavigation();

  const scroller = createRef<ScrollView>();

  const handleUnderstand = () => {
    setUnderstood(true);
  }

  const goToNextPage = () => {
    if (!rightButtonActive) {
      return;
    }
    const newPage = page + 1;
    scroller.current?.scrollTo({ x: deviceWidth * newPage, y: 0, animated: true });
    setPage(newPage);
  }

  const goToPreviousPage = () => {
    if (!leftButtonActive) {
      return;
    }
    const newPage = page - 1;
    scroller.current?.scrollTo({ x: deviceWidth * newPage, y: 0, animated: true });
    setPage(newPage);
  }

  const rightButtonActive = useMemo<boolean>((): boolean => {
    if (page !== numberOfPages) {
      return true;
    } else {
      return false;
    }
  }, [page]);

  const leftButtonActive = useMemo<boolean>((): boolean => {
    if (page !== 0) {
      return true;
    } else {
      return false;
    }
  }, [page]);

  const handleGoBack = useCallback(() => {
    reset({
      routes: [{ name: 'Home' }],
      index: 0,
    });
  }, [reset]);

  return (
    <>
      <PagesContainer
        scrollEventThrottle={200}
        horizontal
        pagingEnabled
        showsHorizontalScrollIndicator={false}
        decelerationRate="normal"
        scrollEnabled={false}
        ref={scroller}
      >
        <PageContainer>
          <PageImage source={require('../../assets/Pages/page1.JPG')} resizeMode="contain"/>
          <PageText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>
            Ferver a água (preferencialmente água mineral ou filtrada).
          </PageText>
        </PageContainer>

        <PageContainer>
          <PageImage source={require('../../assets/Pages/page2.JPG')} resizeMode="contain"/>
          <PageText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>
            Escaldar o filtro de papel no coador.
          </PageText>
        </PageContainer>

        <PageContainer>
          <PageImage source={require('../../assets/Pages/page3.JPG')} resizeMode="contain"/>
          <PageText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>
            Colocar o pó no filtro e dar umas batidas leves para nivelar.
          </PageText>
        </PageContainer>

        <PageContainer>
          <PageImage source={require('../../assets/Pages/page4.JPG')} resizeMode="contain"/>
          <PageText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>
            Colocar o conjunto em cima de uma balança e tarar.
          </PageText>
        </PageContainer>

        <PageContainer>
          <PageImage source={require('../../assets/Pages/page5.JPG')} resizeMode="contain"/>
          <PageText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>
            Colocar 
            <BlueText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>{' ' + gramsOfWaterForBloom}g</BlueText>
            {' '}de água para molhar o café uniformemente.
          </PageText>
        </PageContainer>

        <PageContainer>
          <PageImage source={require('../../assets/Pages/page6.JPG')} resizeMode="contain"/>
          <PageText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>
            Segurar o coador com as duas mãos e gentilmente balançar de forma a rotacionar o pó pelo filtro.
          </PageText>
        </PageContainer>

        <PageContainer>
          <PageImage source={require('../../assets/Pages/page7.JPG')} resizeMode="contain"/>
          <PageText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>
            Esperar até o pó parar de soltar bolhas (40 segundos aproximadamente).
          </PageText>
        </PageContainer>

        <PageContainer>
          <PageImage source={require('../../assets/Pages/page8.JPG')} resizeMode="contain"/>
          <PageText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>
            Colocar o resto da água em formato circular pela superfície do pó (evitar que a água caia direto no filtro de papel).
          </PageText>
        </PageContainer>

        <PageContainer>
          <PageImage source={require('../../assets/Pages/page9.jpg')} resizeMode="contain"/>
          <PageText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>
            Chegando aos 
            <BlueText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>{' ' + gramsOfWater}g</BlueText>
            {' '}na balança, pare de colocar água.
          </PageText>
        </PageContainer>

        <PageContainer>
          <PageImage source={require('../../assets/Pages/page10.JPG')} resizeMode="contain"/>
          <PageText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>
            Espere a água abaixar um pouco e repita o processo de rotação do pó pelo filtro.
          </PageText>
        </PageContainer>

        <PageContainer>
          <PageImage source={require('../../assets/Pages/page11.JPG')} resizeMode="contain"/>
          <PageText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>
            Aguarde até que boa parte da água seja filtrada e retire o coador de cima da caneca/chaleira.
          </PageText>
        </PageContainer>

        <PageContainer>
          <PageImage source={require('../../assets/Pages/page12.JPG')} resizeMode="contain"/>
          <PageText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>
            Aprecie seu delicioso café!
          </PageText>
        </PageContainer>
        
      </PagesContainer>

      <BottomNavigationContainer>
        <AntDesign name="arrowleft" size={40} color={leftButtonActive ? '#0e0e0e' : '#FFE6D3'}/>
        <PageNumberText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>Página {page+1}</PageNumberText>
        <AntDesign name="arrowright" size={40} color={rightButtonActive ? '#0e0e0e' : '#FFE6D3'}/>
      </BottomNavigationContainer>

      <HiddenNavigationButtons>
        <HiddenNavigationButton onPress={goToPreviousPage} />
        <HiddenNavigationButton onPress={goToNextPage} />
      </HiddenNavigationButtons>

      {!understood && 
        <HiddenNavigationButtons style={{backgroundColor: 'rgba(0,0,0,0.9)'}}>
          <HiddenNavigationButton activeOpacity={1} onPress={handleUnderstand} style={{borderRightColor: '#eaeaea', borderRightWidth: 1.5, borderStyle: 'dotted'}}>
            <AntDesign name="arrowleft" size={60} color="#eaeaea"/>
            <HiddenNavigationButtonExampleText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>
              Voltar
            </HiddenNavigationButtonExampleText>
          </HiddenNavigationButton>
          <HiddenNavigationButton activeOpacity={1} onPress={handleUnderstand} style={{borderLeftColor: '#eaeaea', borderLeftWidth: 1.5, borderStyle: 'dotted'}}>
            <AntDesign name="arrowright" size={60} color="#eaeaea"/>
            <HiddenNavigationButtonExampleText style={{ fontFamily: 'ChelseaMarket_400Regular' }}>
              Próximo
            </HiddenNavigationButtonExampleText>
          </HiddenNavigationButton>
        </HiddenNavigationButtons>
      }
      

      <EscapeButton onPress={handleGoBack}>
        <EscapeButtonText style={{fontFamily: 'ChelseaMarket_400Regular'}}>
          X
        </EscapeButtonText>
      </EscapeButton>
    </>
  );
}

export default Instructions;