import React, { useCallback, useState } from 'react';

import { useNavigation } from '@react-navigation/native';

import { AntDesign } from '@expo/vector-icons';

import { useCoffee } from '../../hooks/coffee';

import { 
  Container, 
  Text, 
  BackButtonContainer, 
  BottomContainer,  
  OptionsContainer,
  OptionContainer,
  OptionFrame,
  Image,
  NextButtonContainer,
  NextButtonText,
  OptionText,
} from './styles';

const Informations2: React.FC = () => {
  const [choice, setChoice] = useState<'po' | 'grao' | ''>('');

  const { navigate, goBack } = useNavigation();

  const { setNewTypeOfCoffee } = useCoffee();

  const handleNext = useCallback(() => {
    setNewTypeOfCoffee(choice);
    navigate('Informations3');
  }, [navigate, choice]);

  return (
    <Container>
      <BackButtonContainer onPress={() => goBack()}>
        <AntDesign name="arrowleft" size={40} color="#0e0e0e" />
      </BackButtonContainer>
      <Text style={{fontFamily: 'ChelseaMarket_400Regular'}}>
        Você compra seu café moído ou em grão? 
      </Text>
      <BottomContainer>
        <OptionsContainer>
          <OptionContainer onPress={() => setChoice('po')}>
            <OptionFrame style={choice === 'po' && { borderColor: '#1da1f2' }}>
              <Image source={require('../../assets/po.png')} resizeMode="contain" style={{height: 70}}/>
            </OptionFrame>
            <OptionText style={{fontFamily: 'ChelseaMarket_400Regular'}}>
              Moído
            </OptionText>
          </OptionContainer>
          <OptionContainer onPress={() => setChoice('grao')}>
            <OptionFrame style={choice === 'grao' && { borderColor: '#1da1f2' }}>
              <Image source={require('../../assets/grao.png')} resizeMode="contain" />
            </OptionFrame>
            <OptionText style={{fontFamily: 'ChelseaMarket_400Regular'}}>
              Grão
            </OptionText>
          </OptionContainer>
        </OptionsContainer>
        <NextButtonContainer style={choice === '' && { backgroundColor: '#C8C8C8' }} onPress={handleNext} disabled={choice === ''}>
          <NextButtonText>Pronto</NextButtonText>
        </NextButtonContainer>
      </BottomContainer>
    </Container>
  );
}

export default Informations2;