import React, { useCallback, useState } from 'react';

import { useCoffee } from '../../hooks/coffee';

import { useNavigation } from '@react-navigation/native';

import {
  Container,
  HeaderContainer,
  EscapeButton,
  EscapeButtonText,
  HeaderText,
  MiddleContainer,
  ValueInput,
  CupImage,
  ObservationText,
  NextButton,
  NextButtonText,
} from './styles';

const Setup1: React.FC = () => {
  const [cups, setCups] = useState('');

  const { setNewNumberOfCups } = useCoffee();

  const { reset, navigate } = useNavigation();

  const handleGoBack = useCallback(() => {
    reset({
      routes: [{ name: 'Home' }],
      index: 0,
    });
  }, [reset]);

  const handleTextChange = useCallback((text: string) => {
    if (Number(text) > 300) {
      return;
    }
    if (isNaN(Number(text))) {
      text = text.slice(0, -1);
    }
    setCups(text);
    setNewNumberOfCups(Number(text));
  }, []);

  return (
    <Container>
      <HeaderContainer>
        <EscapeButton onPress={handleGoBack}>
          <EscapeButtonText style={{fontFamily: 'ChelseaMarket_400Regular'}}>
            X
          </EscapeButtonText>
        </EscapeButton>
        <HeaderText style={{fontFamily: 'ChelseaMarket_400Regular'}}>
          Quanto de café você quer fazer?
        </HeaderText>
      </HeaderContainer>

      <MiddleContainer>
        <ValueInput 
          style={{fontFamily: 'ChelseaMarket_400Regular'}} 
          keyboardType="number-pad"
          value={cups}
          onChangeText={handleTextChange}
        />
        <CupImage source={require('../../assets/coffeeCupBlack.png')} resizeMode="contain"/>
      </MiddleContainer>

      <ObservationText style={{fontFamily: 'ChelseaMarket_400Regular'}}>
        Cada xícara equivale a aproximadamente 130ml de café coado.
      </ObservationText>

      <NextButton
        disabled={!cups} 
        onPress={() => navigate('Setup2')} 
        style={!cups && { backgroundColor: '#C8C8C8' }}
      >
        <NextButtonText style={{fontFamily: 'ChelseaMarket_400Regular'}}>
          Próximo
        </NextButtonText>
      </NextButton>
    </Container>
  );
}

export default Setup1;