import React, {
  createContext,
  useCallback,
  useState,
  useContext,
  useEffect,
  useMemo,
} from 'react';

import { AsyncStorage } from 'react-native';

interface ICoffeeContext {
  typeOfCoffee: 'po' | 'grao' | '';
  roastOfCoffee: 'dark' | 'medium' | 'light' | '';
  intensityOfCoffee: 'weak' | 'balanced' | 'intense' | '';
  numberOfCups: number;
  gramsPerLiter: number;
  gramsOfWater: number;
  gramsOfCoffee: number;
  gramsOfWaterForBloom: number;
  setNewTypeOfCoffee(newType: 'po' | 'grao' | ''): void;
  setNewRoastOfCoffee(newRoast: 'dark' | 'medium' | 'light' | ''): void;
  setNewIntensityOfCoffee(newIntensity: 'weak' | 'balanced' | 'intense' | ''): void;
  setNewNumberOfCups(newNumberOfCups: number): void;
  savePreferences(): Promise<void>;
}

const CoffeeContext = createContext<ICoffeeContext>({} as ICoffeeContext);

export const CoffeeProvider: React.FC = ({ children }) => {
  const [typeOfCoffee, setTypeOfCoffee] = useState<'po' | 'grao' | ''>('');
  const [roastOfCoffee, setRoastOfCoffee] = useState<'dark' | 'medium' | 'light' | ''>('');
  const [intensityOfCoffee, setIntensityOfCoffee] = useState<'weak' | 'balanced' | 'intense' | ''>('');
  const [numberOfCups, setNumberOfCups] = useState(0);

  useEffect(() => {
    AsyncStorage.getItem('@PreferenciasUsuario').then(item => {
      if (item) {
        const data = JSON.parse(item);
        setTypeOfCoffee(data.typeOfCoffee);
        setRoastOfCoffee(data.roastOfCoffee);
        setIntensityOfCoffee(data.intensityOfCoffee);
      } else {
        return;
      }
    });
  }, []);

  const gramsPerLiter = useMemo(() => {
    let proportion = 60;
    if (roastOfCoffee === 'dark') {
      proportion -= 10;
    }
    if (roastOfCoffee === 'light') {
      proportion += 10;
    }
    if (intensityOfCoffee === 'intense') {
      proportion += 7;
    }
    if (intensityOfCoffee === 'weak') {
      proportion -= 7;
    }
    return proportion;
  }, [roastOfCoffee, intensityOfCoffee]);

  const gramsOfWater = useMemo(() => {
    return Number((numberOfCups * 130 + numberOfCups * 10).toFixed(2));
  }, [numberOfCups, gramsPerLiter]);

  const gramsOfCoffee = useMemo(() => {
    return Number((gramsOfWater * gramsPerLiter/1000).toFixed(2));
  }, [gramsOfWater, gramsPerLiter]);

  const gramsOfWaterForBloom = useMemo(() => {
    return Number((gramsOfCoffee * 3).toFixed(2));
  }, [gramsOfCoffee]);

  const setNewTypeOfCoffee = useCallback((newType: 'po' | 'grao') => {
    setTypeOfCoffee(newType);
  }, []);

  const setNewRoastOfCoffee = useCallback((newRoast: 'dark' | 'medium' | 'light') => {
    setRoastOfCoffee(newRoast);
  }, []);

  const setNewIntensityOfCoffee = useCallback((newIntensity: 'weak' | 'balanced' | 'intense') => {
    setIntensityOfCoffee(newIntensity);
  }, []);

  const setNewNumberOfCups = useCallback((newNumberOfCups: number) => {
    setNumberOfCups(newNumberOfCups);
  }, []);

  const savePreferences = useCallback(async (): Promise<void> => {
    const newPreferences = {
      typeOfCoffee: typeOfCoffee,
      roastOfCoffee: roastOfCoffee,
      intensityOfCoffee: intensityOfCoffee,
    };
    await AsyncStorage.setItem('@PreferenciasUsuario', JSON.stringify(newPreferences));
  }, [typeOfCoffee, roastOfCoffee, intensityOfCoffee]);

  return (
    <CoffeeContext.Provider
      value={{ 
        typeOfCoffee, 
        gramsOfCoffee, 
        gramsOfWater,  
        gramsOfWaterForBloom,
        gramsPerLiter,
        intensityOfCoffee,
        numberOfCups,
        roastOfCoffee,
        setNewIntensityOfCoffee,
        setNewNumberOfCups,
        setNewRoastOfCoffee,
        setNewTypeOfCoffee,
        savePreferences
      }}
    >
      {children}
    </CoffeeContext.Provider>
  );
};

export function useCoffee(): ICoffeeContext {
  const context = useContext(CoffeeContext);

  return context;
}
