import React, { useState, useEffect } from 'react';

import { View, ActivityIndicator, StyleSheet, AsyncStorage } from 'react-native';

import { createStackNavigator } from '@react-navigation/stack';

import Informations1 from '../pages/Informations1';
import Informations2 from '../pages/Informations2';
import Informations3 from '../pages/Informations3';
import Informations4 from '../pages/Informations4';
import Home from '../pages/Home';
import Setup1 from '../pages/Setup1';
import Setup2 from '../pages/Setup2';
import Instructions from '../pages/Instructions';

const Stack = createStackNavigator();

const Routes = () => {
  const [initialRoute, setInitialRoute] = useState('');

  useEffect(() => {
    AsyncStorage.getItem('@PreferenciasUsuario').then(item => {
      if (item) {
        setInitialRoute('Home');
      } else {
        setInitialRoute('Informations');
      }
    })
  }, []);

  if (initialRoute === '') {
    return (
      <View style={styles.loadingView}>
        <ActivityIndicator color="#0e0e0e"/>
      </View>
    );
  }

  return (
    <Stack.Navigator
      screenOptions={{
        headerShown: false,
        cardStyle: { backgroundColor: '#FFE6D3' },
      }}
      initialRouteName={initialRoute}
      // initialRouteName="Informations1"
    >
      <Stack.Screen name="Informations1" component={Informations1} />
      <Stack.Screen name="Informations2" component={Informations2} />
      <Stack.Screen name="Informations3" component={Informations3} />
      <Stack.Screen name="Informations4" component={Informations4} />
      <Stack.Screen name="Home" component={Home} />
      <Stack.Screen name="Setup1" component={Setup1} />
      <Stack.Screen name="Setup2" component={Setup2} />
      <Stack.Screen name="Instructions" component={Instructions} />
    </Stack.Navigator>
  );
}

const styles = StyleSheet.create({
  loadingView: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: '#FFE6D3',
  }
});

export default Routes;