import React, { useCallback, useState } from 'react';

import { useNavigation } from '@react-navigation/native';

import { AntDesign } from '@expo/vector-icons';

import { useCoffee } from '../../hooks/coffee';

import { 
  Container, 
  Text, 
  BackButtonContainer, 
  BottomContainer,  
  OptionsContainer,
  OptionContainer,
  Image,
  ObservationText,
  NextButtonContainer,
  NextButtonText,
  OptionText,
} from './styles';

const Informations3: React.FC = () => {
  const [choice, setChoice] = useState<'dark' | 'medium' | 'light' | ''>('');

  const { navigate, goBack } = useNavigation();

  const { setNewRoastOfCoffee, typeOfCoffee } = useCoffee();

  const handleNext = useCallback(() => {
    setNewRoastOfCoffee(choice);
    navigate('Informations4');
  }, [navigate, choice]);

  return (
    <Container>
      <BackButtonContainer onPress={() => goBack()}>
        <AntDesign name="arrowleft" size={40} color="#0e0e0e" />
      </BackButtonContainer>
      <Text style={{fontFamily: 'ChelseaMarket_400Regular'}}>
        Qual a torra do seu café?
      </Text>
      <BottomContainer>
        <OptionsContainer>
          <OptionContainer onPress={() => setChoice('light')}>
            <Image source={require('../../assets/lightRoast.png')} resizeMode="contain" style={choice === 'light' && { borderColor: '#1da1f2' }}/>
            <OptionText style={{fontFamily: 'ChelseaMarket_400Regular'}}>
              Clara
            </OptionText>
          </OptionContainer>
          <OptionContainer onPress={() => setChoice('medium')}>
            <Image source={require('../../assets/mediumRoast.png')} resizeMode="contain" style={choice === 'medium' && { borderColor: '#1da1f2' }}/>
            <OptionText style={{fontFamily: 'ChelseaMarket_400Regular'}}>
              Média
            </OptionText>
          </OptionContainer>
          <OptionContainer onPress={() => setChoice('dark')}>
            <Image source={require('../../assets/darkRoast.png')} resizeMode="contain" style={choice === 'dark' && { borderColor: '#1da1f2' }}/>
            <OptionText style={{fontFamily: 'ChelseaMarket_400Regular'}}>
              Escura
            </OptionText>
          </OptionContainer>
        </OptionsContainer>
        <ObservationText style={{fontFamily: 'ChelseaMarket_400Regular'}}>
          Café vendido moído normalmente tem a torra escura.
        </ObservationText>
        <NextButtonContainer style={choice === '' && { backgroundColor: '#C8C8C8' }} onPress={handleNext} disabled={choice === ''}>
          <NextButtonText>Pronto</NextButtonText>
        </NextButtonContainer>
      </BottomContainer>
    </Container>
  );
}

export default Informations3;